// The module 'vscode' contains the VS Code extensibility API
// Import the module and reference it with the alias vscode in your code below
import * as vscode from 'vscode';
//import * as index_html from './terminal_frontend/index.html';

// this method is called when your extension is activated
// your extension is activated the very first time the command is executed
export function activate(context: vscode.ExtensionContext) {
	context.subscriptions.push(
		vscode.commands.registerCommand('catCoding.start', () => {
			// Create and show a new webview
			const panel = vscode.window.createWebviewPanel(
				'catCoding', // Identifies the type of the webview. Used internally
				'Cat Coding', // Title of the panel displayed to the user
				vscode.ViewColumn.One, // Editor column to show the new webview panel in.
				{
					enableScripts: true,
				} // Webview options. More on these later.
			);
			const cssPath = vscode.Uri.file(
				'node_modules/xterm/css/xterm.css'
			);
			const jsPath = vscode.Uri.file(
				'node_modules/xterm/css/xterm.js'
			);
			const cssUri = panel.webview.asWebviewUri(cssPath);
			const jsUri = panel.webview.asWebviewUri(jsPath);

			panel.webview.html = getWebviewContent(cssUri, jsUri);
		})
	);
}

function getWebviewContent(xterm_js: vscode.Uri, xterm_css: vscode.Uri): string {
	return `<!doctype html>
<html>

<head>
    <link rel="stylesheet" href="../../node_modules/xterm/css/xterm.css" />
    <script src="../../node_modules/xterm/lib/xterm.js"></script>
</head>

<body>
    <div id="terminal"></div>
    <script>
        var term = new terminal();
        term.open(document.getelementbyid('terminal'));
        term.write('hello from \x1b[1;3;31mxterm.js\x1b[0m $ ')
    </script>
</body>

</html>`;
}

// this method is called when your extension is deactivated
export function deactivate() { }
